import React, { Component }  from 'react';
import { Link } from "react-router-dom";
import FontAwesome from 'react-fontawesome';
import * as url from '../../../constants/url';
import './style.sass';
class Engine extends Component {
    constructor(props) {
        super(props);
        this.state = {
            engineList : [
                {
                    icon: "star",
                    name: "收藏夹",
                    color: "red",
                    link: url.Collects
                },
                {
                    icon: "shopping-cart",
                    name: "购物车",
                    color: "blue",
                    link: url.ShoppingCart
                },
                {
                    icon: "paw", 
                    name: "...",
                    color: "green",
                    link: ""
                },
                {
                    icon: "motorcycle",
                    name: "...",
                    color: "gold",
                    link: ""
                }
            ]
        }
    }
    render() {
        return (
            <div className="engine-list">
                {this.state.engineList.map((item,index) => {
                    return(
                        <Link className="list-item" key={index} to={item.link ? item.link : ""}>
                            <div style={{color: item.color}}>
                                <FontAwesome name={item.icon}/>
                            </div>
                            <div>
                                {item.name}
                            </div>
                        </Link>
                    )
                })}
            </div>
        );
    }
}

export default Engine;


