import React, { Component } from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as collectAction from '../../actions/collect';
import Head from '../../components/Head';
class Collects extends Component {
    constructor(props) {
        super(props);
    }

    
    componentWillMount() {
        
    }
    

    render() {
        return (
            <div>
                <Head title="收藏夹"/>
                <div>
                    展示收藏的内容，及从收藏夹移除的功能。
                    功能简单，不想重复写。
                </div>
            </div>
        );
    }
}

function mapStateToProps(state) {
    return {
        collectInfo: state.collectInfo
    }
}

function mapDispatchToProps(dispatch) {
    return {
        collectActions: bindActionCreators(collectAction,dispatch)
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(Collects);


