import React, { Component } from 'react';
import Img from '../Img';
import './style.sass';
class UserInfo extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div className="user-info-card">
                <div className="head-image">
                    <Img src="headimage.jpg"/>
                </div>
                <div className="info">
                    {this.props.userInfo.username}
                </div>
            </div>
        );
    }
}

export default UserInfo;


